<template>
  <div id="record-section-poem">
    <header>
      <img src="../../assets/imgs/images/pre.png" @click="goback" />
      <p>读本库</p>
    </header>
    <p class="button">
      <button>{{ $route.query.title }}</button>
      <img @click="goback" src="../../assets/imgs/images/错误提示.png" />
    </p>
    <RecordSection :listData="listData"></RecordSection>
  </div>
</template>

<script>
import RecordSection from "@/components/RecordSection.vue";
export default {
  components: { RecordSection },
  name: "recordSectionPoem",
  data() {
    return {
      id: this.$route.query.id,
      listData: [],
    };
  },
  created() {
    this.$nextTick(()=>{
      this.getListData();
    })
  },
  methods: {
    goback() {
      this.$router.back(-1);
    },
    async getListData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getPoemListApp",
        data: {
          page: 1,
          limit: 12,
          str: "",
          classify_id: this.id,
        },
      });
      this.listData = data.data.data.data;
    },
  },
};
</script>

<style lang="scss" scoped>
#record-section-poem {
  header {
    position: sticky;
    top: 0;
    left: 0;
    padding: 10px 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    img {
      width: 15px;
    }
    p {
      width: 100%;
      font-size: 18px;
      text-align: center;
    }
  }
  .button {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 10px 15px 10px;
    button {
      padding: 8px 13px;
      border-radius: 5px;
      color: #fff;
      border: 0px;
      background-color: #56ba33;
    }
    img {
      width: 30px;
    }
  }
}
</style>